@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("./source/fonts/ptsans.woff2") format("woff2"), url("./source/fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("./source/fonts/ptsansbold.woff2") format("woff2"), url("./source/fonts/ptsansbold.woff") format("woff");
}
html {
  height: 100%;
  font-size: 14px;
  font-family: "PT Sans", "Arial", sans-serif;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;
}
body {
  min-height: 100%;
  background-color: #FFFFFF;
  color: #000000;
  counter-reset: hyphen;
  min-width: 320px;
  margin: 0 auto;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  height: auto;
  overflow: hidden;
}
fieldset {
  padding: 0;
  margin: 0;
}
figure {
  margin: 0;
}
svg {
  fill: currentColor;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
::placeholder {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  font-weight: bold;
  color: #000000;
}
@media (min-width: 768px) {
  body {
    min-width: 768px;
  }
}
@media (min-width: 1200px) {
  body {
    min-width: 1200px;
  }
}
.container {
  width: 320px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 20px;
